/* Header font */
@font-face {
  font-family: 'Rubik';

  src: 
  /* IE9*/
  url('../../assets/fonts/header/Rubik.eot');
  
  src: 
    /* IE6-IE8 */
    url('../../assets/fonts/header/Rubik.eot?#iefix')
      format('embedded-opentype'),
    /* chromeãfirefox */ 
    url('../../assets/fonts/header/Rubik.woff2')
      format('woff2'),
    /* chromeãfirefox */ 
    url('../../assets/fonts/header/Rubik.woff')
      format('woff'),
    /* chromeãfirefoxãoperaãSafari, Android, iOS 4.2+*/
    url('../../assets/fonts/header/Rubik.ttf')
      format('truetype'),
    /* iOS 4.1- */ 
    url('../../assets/fonts/header/Rubik.svg#Rubik')
      format('svg');
}

