/* _title-area.scss */

/* HeroTitleAreaViewContainer */

.hero-title-area-view-container {
  position: absolute;
  left: 115px;
  top: 275px;
}

/* HeroTitleTextContainer */

.hero-title-text-container {
  position: absolute;
  left: 0;
  top: 0;
  width: 320px;
  height: 29px;
}

.hero-title-text {
  font-family: Rubik;
  font-style: normal;
  font-weight: 500;
  font-size: 39px;
  line-height: 46px;
  letter-spacing: 0.04em;
  text-indent: 3px;

  color: #222222;
  mix-blend-mode: normal;
}

/* HeroSloganTextContainer */

.hero-slogan-text-container {
  position: absolute;
  left: 131px;
  top: 54px;
  width: 198px;
  height: 22px;
}

.hero-slogan-text {
  font-family: SofiaProSoftW01-Regular;
  font-size: 22px;
  line-height: 28px;
  /* identical to box height */

  letter-spacing: 0.06em;
  text-indent: 3px;

  color: #9e9eff;
}

/* HeroEventInfoTextContainer */

.hero__event-info-container {
  position: absolute;
  left: 0;
  top: 152px;
  width: 286px;
  height: 114px;
}

.hero__event-info-text {
  position: absolute;
  width: 22vw;
  height: 64px;
  @include font-source-body(
    $size: 1em,
    $color: #adadad,
    $weight: normal,
    $lineHeight: 0.75
  );
  white-space: nowrap;
}

/* HeroSponsorUsButtonContainer */

.hero-sponsor-us-button-container {
  position: absolute;
  left: 0;
  top: 266px;
  width: 183px;
  height: 46px;
}

.hero-sponsor-us-button {
  background: #6f6fe8;
  box-shadow: 0px 2px 6px #bfbfbf;
  border-radius: 23px;
}

/* HeroSponsorUsButtonText */

.hero-sponsor-us-button-text {
  position: absolute;
  left: 41px;
  top: 17px;
  width: 94px;
  height: 11px;

  font-family: SofiaProSoftW01-Regular;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.07em;
  text-indent: 3px;

  fill: #fffefe;
}

/* NavigateToBottomContainer */

.navigate-to-bottom-container {
  position: absolute;
  left: -16px;
  top: 362px;
  width: 66px;
  height: 66px;
}

.navigate-to-bottom-outer-circle {
  position: absolute;
  left: 0;
  top: 0;
}

.navigate-to-bottom-middle-circle {
  position: absolute;
  left: 11px;
  top: 11px;
}

.navigate-to-bottom-inner-circle {
  position: absolute;
  left: 21px;
  top: 21px;
}

.navigate-to-bottom-arrow {
  position: absolute;
  left: 25px;
  top: -5px;
}
